form {
  max-width: 250px;
  border: solid 1px black;
  padding: 20px;
}

label {
  display: block;
  font-size: 14px;
  margin-bottom: 5px;
  font-size: 16px;
}

input {
  margin-bottom: 15px;
  width: 100%;
  height: 30px;
  border-radius: 5px;
  font-size: 16px;
  padding-left: 10px;
  box-sizing: border-box;
}

button {
  display: block;
  padding: 5px 10px;
  font-size: 16px;
  border-radius: 10px;
  background-color: darkkhaki;
  cursor: pointer;
  margin-left: auto;
}
